<template>
  <div class="upgrade">
    <HeaderCon :propData="propData"></HeaderCon>
    <div class="upgrade-title">猜鸡藕</div>
    <div class="upgrade-explanation">促销产品为私人订制款，确定下单后不可退货</div>
    <div class="pro-list">
      <div class="pro-box">
        <div class="pro-price">{{data.origin_product.price_yh}}</div>
        <img class="pro-img" :src="data.origin_product.photo_x"/>
        <div class="pro-txt">{{data.origin_product.name }}</div>
      </div>
      <div class="pro-box">
        <div class="pro-price">{{data.up_product.price_yh}}</div>
        <img class="pro-img" :src="data.up_product.photo_x"/>
        <div class="pro-txt">{{data.up_product.name}}</div>
      </div>
    </div>
    <!-- 开奖计时 -->
    <div class="upgrade-countdown">
      <div>第<span class="upgrade-red">{{num}}</span>期</div>
      <div>开奖倒计时<span class="upgrade-red">{{time}}</span></div>
    </div>
    <div class="upgrade-choose">
      <img @click="jfunc" class="choose-img" :src="jimg[j]"/>
      <img @click="ofunc" class="choose-img" :src="oimg[o]"/>
    </div>
    <mt-button @click="upgradeBtn" class="upgrade-btn">立即升级</mt-button>
    <!-- 已开奖记录 -->
    <ul class="award-record">
      <li class="award-head">
        <div class="award-td">开奖时间</div>
        <div class="award-td">开奖期号</div>
        <div class="award-td">开奖号码</div>
        <div class="award-td">奇/偶</div>
      </li>
      <li class="award-tr" v-for="(item, index) in data.kaijiang_list">
        <div class="award-td">{{item.opentime}}</div>
        <div class="award-td">{{item.expect}}</div>
        <div class="award-td">{{item.opencode}}</div>
        <div class="award-td">{{ item.result == 'odd' ? '鸡' : '藕' }}</div>
      </li>
    </ul>

    <!--模态眶-->
    <mt-popup v-model="is_show_model">
      <div class="msg-box">
        <div class="msg-close" @click="closeBox"></div>
        <div class="msg-title">升级中</div>
        <div class="msg-txt">升级中，请等待第{{num}}期开奖结果</div>
      </div>
    </mt-popup>
  </div>

</template>

<script>
  import {yuanAjax, getCookie} from '../js/common.js';
  import HeaderCon from '@/components/HeaderCon';
  export default {
    name: 'upgrade',
    data() {
      return {
        propData: {
          title: '猜鸡偶',
          link: ''
        },
        data: {
          origin_product: {
            price_yh: ''
          },
          up_product: {
            price_yh: ''
          }
        },
        list: [],
        jimg: ['data:image/png;base64,', 'data:image/png;base64,'],
        oimg: ['data:image/png;base64,', 'data:image/png;base64,'],
        j: 0,
        o: 1,
        time: '00:00:00', // 倒计时
        timers: '',
        num: '',
        is_show_model: false
      }
    },
    components: {
      HeaderCon
    },
    methods: {
      jfunc(){
        this.j = 0;
        this.o = 1
      },
      ofunc(){
        this.j = 1;
        this.o = 0
      },
      //倒计时
      Timer (maxtime, callback) {
        var that = this;
        //maxtime：时间，单位s
        //id：显示计时器信息的容器id
        //callback：计时器结束回调
        var tmp, day, hours, minutes, seconds;

        function CountDown() {
          // console.log(maxtime)
          if (maxtime >= 0) {
            day = Math.floor(maxtime / (60 * 60 * 24));
            tmp = maxtime - day * 60 * 60 * 24;
            hours = Math.floor(tmp / (60 * 60));
            tmp = tmp - hours * 60 * 60;
            minutes = Math.floor(tmp / (60));
            tmp = tmp - minutes * 60;
            seconds = tmp;
            maxtime -= 1;
            that.time = that.MillisecondToDate(parseInt(maxtime))
          }
          else {
            clearInterval(timer);
            if (typeof callback == "function") callback(); //执行倒计时完成后的回调
          }
        }

        var timer = setInterval(function () {
          if (maxtime > 0) {
            CountDown()
          }
        }, 1000);
        that.timers = timer;

      },
      //毫秒转格式
      MillisecondToDate (value) {
        var theTime = parseInt(value);// 秒
        var theTime1 = 0;// 分
        var theTime2 = 0;// 小时
        // alert(theTime);
        if (theTime > 60) {
          theTime1 = parseInt(theTime / 60);
          theTime = parseInt(theTime % 60);
          if (theTime1 > 60) {
            theTime2 = parseInt(theTime1 / 60);
            theTime1 = parseInt(theTime1 % 60);
          }
        }
        var result = String(theTime).length == 1 ? "0" + parseInt(theTime) : parseInt(theTime);//秒
        if (theTime1 > 0) { // 分
          String(theTime1).length == 1 ? result = "0" + parseInt(theTime1) + ":" + result : result = "" + parseInt(theTime1) + ":" + result;
        } else {
          result = '00:' + result;
        }
        if (theTime2 > 0) { //小时
          String(theTime2).length == 1 ? result = "0" + parseInt(theTime2) + ":" + result : result = "" + parseInt(theTime2) + ":" + result;
        } else {
          result = '00:' + result;
        }
        return result;
      },
      //立刻升级
      upgradeBtn(){
        var _this = this;
        var kj = _this.j ? 'even' : 'odd';

        yuanAjax(_this, 'Api/Cai/do_cai', {
          uid: getCookie('chaUID'),
          order_sn: _this.$route.query.order_sn,
          pid: _this.$route.query.pid,
          cai: kj
        }, function (res) {
          if (res.status == 1) {
            _this.is_show_model = true;
            setTimeout(function () {
              _this.$router.push({path: '/order'});
            },1000)
          } else {
            _this.$toast({
              message: res.err,
              duration: 1000
            });
          }
        })
      },
      closeBox(){
        this.is_show_model = false;
      }
    },
    mounted: function () {
      var _this = this;
      yuanAjax(_this, 'Api/Cai/index', {
        uid: getCookie("chaUID"),
        order_sn: _this.$route.query.order_sn
      }, function (res) {
        if (res.status == 1) {
          _this.data = res.data;
          _this.num = res.data.number
          _this.Timer(parseFloat(res.data.next_time), function () {
            _this.time = '00:00:00';
          });
        } else {
          _this.$toast({
            message: res.err,
            duration: 1000
          });
        }
      })
    }

  }
</script>

<style lang="less" scoped rel="stylesheet/less">
  .upgrade {
    width: 750px;
    min-height: 100vh;
    background: url(../assets/images/upgrade-bg.jpg) no-repeat;
    background-size: 100%;
    overflow: hidden;
    /*猜鸡藕*/
    .upgrade-title {
      width: 289px;
      height: 92px;
      font-size: 60px;
      margin: 64px auto 30px auto;
      color: #b16b2c;
      text-align: center;
      line-height: 92px;
      background: url(../assets/images/large-lace.png) no-repeat;
      background-size: cover;
    }
    /*说明*/
    .upgrade-explanation {
      min-height: 92px;
      font-size: 28px;
      padding: 0 30px;
      text-align: center;
      letter-spacing: 1px;
      line-height: 46px;
    }
    /* 两款产品 */
    .pro-list {
      width: 750px;
      height: 400px;
      margin-top: 15px;
      background: url(../assets/images/upgrade-sbg.png) no-repeat;
      background-size: 100%;
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      padding: 0 30px;
      box-sizing: border-box;
      overflow: hidden;
    }
    .pro-box {
      width: 280px;
      height: 100%;
      overflow: hidden;
    }
    .pro-price {
      width: 160px;
      height: 52px;
      text-align: center;
      line-height: 52px;
      border-radius: 50px;
      color: #fff49c;
      background: #e53f55;
      font-size: 36px;
      margin: 15px auto 18px auto;
    }
    .pro-img {
      width: 200px;
      height: 200px;
      margin: auto;
      display: block;
      border-radius: 8px;
    }
    .pro-txt {
      font-size: 24px;
      color: #fff;
      text-align: center;
      width: 100%;
      overflow: hidden;
      height: 65px;
      line-height: 65px;
    }
    /*开奖倒计时*/
    .upgrade-countdown {
      width: 750px;
      height: 100px;
      line-height: 120px;
      padding: 0 82px;
      display: flex;
      justify-content: space-between;
      box-sizing: border-box;
      font-size: 28px;
      text-align: center;
    }
    .upgrade-countdown div {
      width: 280px;
    }
    .upgrade-red {
      padding: 0 8px;
      color: #c51825;
    }
    /* 鸡藕选择icon */
    .upgrade-choose {
      display: flex;
      justify-content: space-between;
      width: 750px;
      box-sizing: border-box;
      padding: 13px 170px;
    }
    .choose-img {
      width: 150px;
      height: 150px;
      display: block;
    }
    /*立即升级按钮*/
    .upgrade-btn {
      width: 300px;
      height: 88px;
      line-height: 88px;
      color: #fff;
      background: #e53f55;
      border-radius: 10px;
      margin: 60px auto 70px auto;
      font-size: 30px;
      display: block;
    }
    /* 已开奖记录 */
    .award-record {
      width: 750px;
      overflow: hidden;
      font-size: 24px;
    }
    .award-record::before {
      content: "已开奖记录";
      display: block;
      background: #fff;
      border-bottom: 1px solid #c5c3c4;
      height: 80px;
      line-height: 80px;
      color: #b16b2c;
      font-size: 30px;
      padding-left: 25px;
    }
    .award-head, .award-tr {
      height: 60px;
      line-height: 60px;
      display: flex;
      justify-content: space-between;
      text-align: center;
    }
    .award-head {
      background: #e5e5e5;
      color: #999;
    }
    .award-tr:nth-child(odd) {
      background: #fff;
    }
    .award-tr:nth-child(even) {
      background: #f2f2f2;
    }
    .award-td:nth-of-type(1) {
      width: 276px;
    }
    .award-td:nth-of-type(2) {
      width: 204px;
    }
    .award-td:nth-of-type(3) {
      width: 154px;
    }
    .award-td:nth-of-type(4) {
      width: 116px;
    }
    .award-head .award-td:first-child {
      text-align: left;
      text-indent: 25px;
    }
    .model-bg {
      position: fixed;
      width: 750px;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      background: rgba(0, 0, 0, 0.6);
    }
    .model-wrap {
      width: 610px;
      min-height: 270px;
      box-sizing: border-box;
      padding: 20px;
      background: #fff;
      top: 410px;
      left: 70px;
      position: absolute;
    }
    .model-title {
      color: #b16b2c;
      font-size: 32px;
      text-align: center;
    }
    .model-txt {
      color: #000;
      font-size: 28px;
      text-align: center;
      line-height: 45px;
      word-wrap: wrap;
      padding: 30px 80px;
    }
    .model-btn-upgrade {
      width: 275px;
      height: 70px;
      line-height: 70px;
      font-size: 28px;
      color: #fff;
      text-align: center;
      border-radius: 10px;
      letter-spacing: 2px;
      background: #ad090b;
      margin: auto;
    }

    .model-close {
      width: 60px;
      height: 60px;
      position: absolute;
      right: -28px;
      top: -28px;
      background: url(http://upload-images.jianshu.io/upload_images/5869240-7d704bf362286d98.png) no-repeat;
      background-size: 100%;
    }.msg-box {
       width: 610px;
       background: #fff;
       box-sizing: border-box;
       padding: 20px;
       text-align: center;
       position: relative;
       .msg-title {
         font-size: 32px;
         color: #b16b2c;
         line-height: 70px;
       }
       .msg-txt {
         font-size: 28px;
         color: #000;
         line-height: 82px;
         span {
           color: #ad090b;
         }
       }
       .msg-links {
         display: flex;
         justify-content: space-between;
         .msg-link {
           width: 170px;
           height: 72px;
           line-height: 72px;
           text-align: center;
           color: #fff;
           font-size: 28px;
           border-radius: 10px;
           margin: 20px 0;
         }
         .msg-link:nth-of-type(1) {
           background: #b16b2c;
         }
         .msg-link:nth-of-type(2) {
           background: #ad090b;
         }
         .msg-link:nth-of-type(3) {
           background: #ad090b;
         }
       }
       .msg-close {
         width: 60px;
         height: 60px;
         background: url(../assets/images/msg-close.png) no-repeat;
         background-size: cover;
         position: absolute;
         right: -26px;
         top: -26px;
       }
     }
  }

</style>



